import React from 'react'
import GithubIcon from 'react-icons/lib/go/mark-github'
import FaFacebookSquare from 'react-icons/lib/fa/facebook-square'
import FaLinkedinSquare from 'react-icons/lib/fa/linkedin-square'
import FaTwitterSquare from 'react-icons/lib/fa/twitter-square'
import FaCopyright from 'react-icons/lib/fa/copyright'

import './GetSupportButton.scss'

export default function GetSupportButton() {
  return (
    <div
      className="GetSupportButton"
      href="https://github.com/rigoblock"
    >
    <a href="https://github.com/rigoblock">
      <GithubIcon className="icon" />
    </a>
    <a
      href="https://github.com/rigoblock"
    >
      <FaFacebookSquare className="icon" />
    </a>
    <a
      href="https://github.com/rigoblock"
    >
      <FaLinkedinSquare className="icon" />
    </a>
    <a
      href="https://github.com/rigoblock"
    >
      <FaTwitterSquare className="icon" />
    </a>
    </div>
  )
}

